<template>
	<div class="create">
		<mt-header title="兴趣爱好" class="createName">
			<router-link to="/informate" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<div class="msg">
			<div class="hobby">
				<mytags :items="hobbys" :type="'check'" v-model="form.hobby_list"></mytags>
			</div>
			<div class="hobby">
				<h3>职业</h3>
				<mytags :items="jobs" :type="'check'" v-model="form.job_list"></mytags>
			</div>
		</div>
		<div class="next">
			<!-- <router-link to="/"> -->
				<mt-button class="next-btn" @click="submit">创建账户</mt-button>
			<!-- </router-link> -->
		</div>
	</div>
</template>
<script>
	import mytags from "../my-tags.vue"
	export default {
		components:{mytags},
		data (){
			this.getHobby();
			this.getJob();
			const form = Object.assign(
				{
					hobby_list:"",
					job_list: "",
				},
				JSON.parse(window.localStorage.form)
			)
			return {
				form,
				hobbys:[],
		    	jobs: []
			}
		},
		methods: {
			getHobby (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/hobbys");
			  resource.get().then((callback) => {
			  	callback.data.map(nav =>
						this.hobbys.push(
							{
								text:nav,
								value:nav
							}
						))
			  });
			},
			getJob (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/jobs");
			  resource.get().then((callback) => {
			  	callback.data.map(nav =>
						this.jobs.push(
							{
								text:nav,
								value:nav
							}
						))
			  });
			},
			submit(){
				var resource = this.$resource(this.$store.state.serverIP +"/users/register");
			  // POST someItem/1
			  resource.save(this.form).then((response) => {
			    // success callback
			  }, (response) => {
			    // error callback
			  });


			}
    }
	}
</script>
<style>
.hobby{
	margin-top: 10px;
	overflow: hidden;
}

.hobby h3{
	text-align: center;
	height: 40px;
	line-height: 40px;
	border-bottom: 1px solid #ccc;
}
</style>



