<template>
	<div>
		<mt-header fixed title="管理病例">
  		  <router-link to="/index" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="/uploder" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25">
  		  </router-link>
  		</mt-header>
		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
  		<div style="margin-top:49px;width:98%;margin-left:1%;">
			<mt-search v-model="value" ></mt-search>
			<p><img src="../../assets/hot.png" alt=""style='vertical-align: middle;'>关键字</p>
			<div>
				<button v-for="button in buttons" class="btn_s">{{button.name}}</button>
			</div>
			<!-- 新增按钮 -->
			<div class="next">
				<router-link to="/uploder" slot="right">
					<mt-button class="next-btn" @click="dialogVisible = true">创建新病例</mt-button>
  		  		</router-link>
			</div>
			<div>
				<div></div>
				<div>
					<div></div>
				</div>
			</div>
			<!-- end -->
		</div>
	</div>
</template>
<script>
	export default{
		data(){
			// var buttons = [{name:'体检报告'},{name:'CT'},{name:'B超'},{name:'血常规'}];
			this.getKill();
			return {
				value:null,
				buttons:[],
				sheetVisible: false,
				actions: []
			}
		},
		methods:{
			getKill(){
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/all_medical_records");
				resource.get({user_id:2}).then((response)=>{
					this.buttons=response.data;
				},(error)=>{

				})
			}
		},
	}
</script>
<style>
	.mint-searchbar-core{
		width: 100%;
	}
	.mint-search{
		height: 9vh !important;
	}
	li{
		list-style: none;
	}
	.btn_s{
		margin-right: 3%;
		border-radius: 4px;
		background: #fff;
		height: 41px;
		padding: 2% 5%;
		border-style: none;
		border: 1px solid #d3dce6;
		box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
	}
</style>


