<template>
	<div>
		<mt-header fixed title="照片预览">
  		  <router-link to="/mge_caese" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		</mt-header>
  		<div style="margin-top:50px;line-height: 36px;">
  			<el-row :gutter="20" style="margin:0;">
	  		  <el-col :span="4">名称</el-col>
	  		  <el-col :span="20">
	  		  	<div><input type="text" class='name_t'></div>
	  		  </el-col>
	  		</el-row>
	  		<el-row :gutter="20" style="margin:0;"> 
	  		  <el-col :span="4">分类</el-col>
	  		  <el-col :span="20">
	  		  	<div><el-select v-model="value" placeholder="请选择">
				    <el-option
				      v-for="item in options"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				  </el-select></div>
	  		  </el-col>
	  		</el-row>
  			<el-upload
  			  action="//jsonplaceholder.typicode.com/posts/"
  			  type="drag"
  			  :thumbnail-mode="true"
  			  :on-preview="handlePreview"
  			  :on-remove="handleRemove"
  			  :default-file-list="fileList"
  			>
  			  <i class="el-icon-upload"></i>
  			  <div class="el-dragger__text"><em>点击上传</em></div>
  			</el-upload>
  			<button class="upload_btn" >上传</button>
  		</div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
             fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],

              options: [{
		          value: '选项1',
		          label: '体检报告'
		        }, {
		          value: '选项2',
		          label: 'CT'
		        }, {
		          value: '选项3',
		          label: 'B超'
		        }, {
		          value: '选项4',
		          label: '血常规'
		        }],
			value:''
			}
		},
		methods:{
			handleRemove(file, fileList) {
		        console.log(file, fileList);
		      },
	      	handlePreview(file) {
	        	console.log(file);
	      	},

		}
	}
</script>
<style>
	.el-upload{
		margin-left:2%;
		width: 100% ;
		margin-top: 40px;
	}
	.el-dragger{
		width: 96%;
		height: 280px;
	} 
	.name_t{
		border: 1px solid #c0ccda;
		height: 30px;border-radius: 4px;
		margin-bottom: 20px;
	}
	.upload_btn{
		width: 90%;
	    margin-left: 5%;
	    padding: 10px;
	    font-size: 18px;
	    border-radius: 14px;
	    border-style: none;
	    background: #A21A28;
	    color: #fff;
	    font-weight: bold;
	    margin-top: 40px;
	}
	.el-dragger__cover__interact .btn{
		margin-top: 105px;
	}
	.el-dragger .el-icon-upload{
		margin: 98px 0 16px;
	}
</style>


