<template>
	<div class="create">
		<mt-header title="基本信息" class="createName">
			<router-link to="/seeInformation" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<div class="editinfo">
			<mt-field label="姓名" placeholder="请输入姓名" v-model="form.truename"></mt-field>
			<mt-cell title="性别" class="informate">
				<el-radio class="radio" v-model="form.sex" label="1">男</el-radio>
				<el-radio class="radio" v-model="form.sex" label="2">女</el-radio>
			</mt-cell>
			<mt-cell title="年龄" class="age-input">
				<el-input type="number" v-model="form.age"></el-input>
				<span>岁</span>
			</mt-cell>
			<mt-cell title="血型" class="el-btn el-but">
				<mytags :items="blood_types" :type="'radio'" v-model="form.blood_type">
			  </mytags>
			</mt-cell>
			<mt-cell title="子女数" class="el-btn el-buts">
				<mytags :items="childrens" :type="'radio'" v-model="form.children">
			  </mytags>
			</mt-cell>
			<mt-cell title="兴趣爱好" class="el-btn el-buts">
				<mytags :items="hobbys" :type="'check'" v-model="form.hobby_list"></mytags>
			</mt-cell>
			<mt-cell title="职业" class="el-btn el-buts">
				<mytags :items="jobs" :type="'check'" v-model="form.job_list"></mytags>
			</mt-cell>
		</div>
		<div class="next save">
			<!-- <router-link to="/"> -->
				<mt-button class="next-btn" @click="saveSubmit">保存</mt-button>
			<!-- </router-link> -->
		</div>
	</div>
</template>
<script>
import mytags from "../my-tags.vue"
export default {
		components:{mytags},
		data (){
			this.getHobby();
			this.getJob();
			return {
				form:{
					truename:"",
					sex: '',
					age:"",
					children:"",
					blood_type:"",
					hobby_list:"",
					job_list: ""
				},
				childrens:[
					{
						text:"1",
						value:"1"
					},{
						text:"2",
						value:"2"
					},{
						text:"3",
						value:"3"
					},{
						text:"其他",
						value:"其他"
					}],
				blood_types: [
					{
						value:"0",
						text:"A"
					},
					{
						value:"1",
						text:"B"
					},
					{
						value:"2",
						text:"AB"
					},
					{
						value:"3",
						text:"O"
					},
					{
						value:"4",
						text:"其他"
					}],
				hobbys:[],
				jobs: []
			}
		},
		methods: {
			getHobby (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/hobbys");
			  	resource.get().then((callback) => {
			  	callback.data.map(nav =>
						this.hobbys.push(
							{
								text:nav,
								value:nav
							}
						))
			  	});
			},
			getJob (){
				var resource = this.$resource(this.$store.state.serverIP +"/commons/jobs");
			  	resource.get().then((callback) => {
			  	callback.data.map(nav =>
						this.jobs.push(
							{
								text:nav,
								value:nav
							}
						))
			  	});
			},
			saveSubmit (){
				var resource = this.$resource(this.$store.state.serverIP +"/users/register");
			  	// POST someItem/1
			  	resource.save(this.form).then((response) => {
			    	// success callback
			  	}, (response) => {
			    	// error callback
			  	});
			}
		}
	}
</script>
<style>
.editinfo{
	padding:5px 10px;
}
.save{
	margin-top: 0px;
}

</style>



