<template>
	<div class="create">
		<mt-header title="家人" class="createName">
			<router-link to="/focusFamily" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<div class="focus">
			<mt-field label="称呼" placeholder="请输入称呼" v-model="family.appellation"></mt-field>
			<mt-field label="手机号码" placeholder="请输入手机号码" v-model="family.tel"></mt-field>
		</div>
		<div class="next">
			<router-link to="/focusFamily">
				<mt-button class="next-btn" @click.native="submit">确定</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				family: {
					appellation: '',
					tel: '',
					user_id:1
				}
			}
		},
		methods:{
			submit () {
				//提交被关注人接口
				var resource = this.$resource( 'http://172.16.100.210:3000/api/v1/user_focus/add_focu')
				resource.save(this.family).then((response) => {
					this.$message({
          				message: '提交成功',
          				type: 'success'
        			});
				}, (response) => {
					this.$message({
          				message: response.data.error,
          				type: 'warning'
        			});
				})
			}
		}
	}
</script>
<style>
.focus{
	padding: 10px;
}
</style>



