<template>
	<div>
		<mt-header fixed title="服药管理">
  		  <router-link to="/index" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="" slot="right">
  			<img src="../../assets/addicon.png" width="25" height="25" @click="dialogVisible = true">
  		  </router-link>
  		</mt-header>
  		<div style="margin-top:49px;">
  			<!-- 弹框内容 -->
  			<el-dialog title="新增药品" v-model="dialogVisible" size="tiny">
			  <el-row :gutter="20" class="dialog_s">
			  	<el-col :span="8">药品名称</el-col>
			  	<el-col :span="16"><input type="text" v-model="form.name"></el-col>
			  </el-row>
			  <el-row :gutter="20" class="dialog_s">
			  	<el-col :span="8">用法</el-col>
			  	<el-col :span="16"><input type="text" style="width:30%;" v-model="form.usage">次/日</el-col>
			  </el-row>
			  <el-row :gutter="20" class="dialog_s">
			  	<el-col :span="8">用量</el-col>
			  	<el-col :span="16"><input type="text" style="width:30%;" v-model="form.dosage">片/次</el-col>
			  </el-row>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="dialogVisible = false">取 消</el-button>
			    <el-button type="primary"  @click="submit()">确 定</el-button>
			  </span>
			</el-dialog>
  			<!-- 弹框内容 end  -->
  			<el-row :gutter="20" style="margin:15px 2px;text-align:center;" v-for='item in medicines'>
  				<el-col :span="6">{{item.name}}</el-col>
  				<el-col :span="6">{{item.usage}}次/日</el-col>
  				<el-col :span="6">{{item.dosage}}片/次</el-col>
  				<el-col :span="6">
  					<router-link :to="{name:'addMedicine',params:{id:item.id,name:item.name}}">
  						<el-button size="small" class="button_1">提醒</el-button>
  					</router-link>
  				</el-col>
  			</el-row>
  			<div class="next">
				<mt-button class="next-btn" @click="dialogVisible = true">添加服药项目</mt-button>
			</div>
  		</div>
	</div>
</template>
<script>
	export default{
		data(){
			this.getSubmit();
			return{
				dialogVisible:false,
				medicines:[],
				form:{}
			}
		},
		methods:{
			//读取数据列表
			getSubmit(){
				var resource = this.$resource(this.$store.state.serverIP +"/take_medicine_managements/all_take_medicines");
		    	resource.get({user_id:2}).then((response)=>{
		    		this.medicines = response.data;
		    		console.log(this.medicines)
		    	},(error)=>{

		    	})
			},
			//提交添加新数据
			submit(){
				this.form.user_id = 2;
				var resource = this.$resource(this.$store.state.serverIP+'/take_medicine_managements/add_take_medicine_management');
				resource.save(this.form).then((response)=>{
					response.data
					console.log(response.data)
					this.dialogVisible = false;
					this.getSubmit()
					this.form =[];
				},(error)=>{

				})
			}
		}
	}
</script>
<style>
	.el-dialog--tiny{
		width: 90%;
	}
	.dialog_s input{
		border:1px solid #8492a6;
		margin-bottom: 20px;
		height: 30px;
		border-radius: 5px;
		margin-right: 12px;
	}
	.el-dialog__header{
		text-align: center;
	}
	.el-dialog__footer{
		text-align: center;
	}
</style>


