<template>
  <el-row>
    <el-col :span="8" v-for="item in healthList">
      <router-link :to="{name:'itemDetails',params:{item_id:item.id,title:item.name}}" style="text-decoration:none;">
        <div class="cards">
            {{item.name}}
        </div>
      </router-link>
    </el-col>
  </el-row>
</template>

<script>
export default {
  data (){
    this.getHealthitem();
    return {
      healthList:[]
    }
  },
  methods:{
    getHealthitem (){
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/all_health_items?user_id=2&status=0");
      resource.get().then((callback) => {
        this.healthList = callback.data
      });
    },

  }
}

</script>

<style>
.cards {
  display: block;
  padding:16px 8px;
  margin:10px auto;
  text-align: center;
  width: 60%;
  color: #323232;
  border: 1px solid #d3dce6;
  border-radius: 4px;
  background-color: #fff;
  overflow: hidden;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
}
</style>



