<template>
	<div class="create">
		<mt-header title="家人" class="createName">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
			<router-link to="/addCall" slot="right">
				<span><img src="../../assets/addicon.png" alt="add" width="20" height="20"></span>
			</router-link>
		</mt-header>
		<div class="focus">
			<mt-cell v-for="item in focusForm" :title="item.appellation" v-if="item.whether==0">
				<p v-if="item.whether==0">请求关注</p>
	  			<mt-button class="focus_btn" type="danger" size="small" color="#A21A28" v-if="item.whether==0" @click.native="item.whether=1;" slot="right">关注</mt-button>
	  			<mt-button class="remove_btn" type="default" size="small" v-if="item.whether==0" @click.native="item.whether=1;" slot="right">忽略</mt-button>
	  			<span v-if="item.whether==1" slot="right">
	  				<img src="../../assets/xin.png" alt="xin" width="24" height="24">
	  			</span>
	  			<img class="photo" slot="icon" src="../../assets/logo.png">
			</mt-cell>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			this.getFocusForm();
			return {
				id:1,
				focusForm: []
			}
		},
		methods: {
			getFocusForm: function () {
				var resource = this.$resource('http://172.16.100.210:3000/api/v1/user_focus/list_focu'+'?user_id=1');
				resource.get().then((response) => {
					console.log(response.data);
					this.focusForm=response.data;
					//whether:处理结果0未处理1同意2忽略
				});
			}
		}
	}
</script>
<style>
.focus{
	padding: 10px;
}
.focus .mint-cell-title{
	width: 100px !important;
}
.focus .mint-cell-right button{
	margin-left: 10px;
	height: 26px;
	padding: 0 9px;
	font-size: 13px;
}
.focus .mint-cell-value p{
	position: absolute;
	left: 45px;
	font-size: 14px;
	top: 20px;
}
.focus .mint-cell-right{
	right: 115px;
	top: 6px;
}
.focus .mint-cell-right .focus_btn{
	background-color: #A21A28;
}
.focus .mint-cell-right .remove_btn{
	background-color: #fff;
	border: 1px solid #000;
}
.focus .mint-cell-right span{
	position: absolute;
	right: -100px;
}
.photo{
	width: 32px;
	height: 32px;
	border-radius: 25px;
}
</style>



