<template>
	<div class="create">
		<mt-header title="基本信息" class="createName">
			<router-link to="/register" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<div class="msg">
			<mt-field label="姓名" placeholder="请输入姓名" v-model="form.truename"></mt-field>
			<mt-cell title="性别" class="informate">
				<el-radio class="radio" v-model="form.sex" label="1">男</el-radio>
				<el-radio class="radio" v-model="form.sex" label="2">女</el-radio>
			</mt-cell>
			<mt-cell title="年龄" class="age-input">
				<el-input type="number" v-model="form.age"></el-input>
				<span>岁</span>
			</mt-cell>
			<mt-cell title="血型" class="el-btn el-but">
				<mytags :items="blood_types" :type="'radio'" v-model="form.blood_type">
			  </mytags>
			</mt-cell>
			<mt-cell title="子女数" class="el-btn el-buts">
				<mytags :items="childrens" :type="'radio'" v-model="form.children">
			  </mytags>
			</mt-cell>
		</div>
		<div class="next">
			<router-link to="/hobby">
				<mt-button class="next-btn" @click="clickNext">下一步</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	import mytags from "../my-tags.vue"
	export default {
		components:{mytags},
		data (){
			const form = Object.assign(
				{
					truename:"",
					sex: '',
					age:"",
					children:"",
					blood_type:"",
				},
				JSON.parse(window.localStorage.form)
			)
			return {
				form,
				childrens:[
					{
						text:"1",
						value:"1"
					},{
						text:"2",
						value:"2"
					},{
						text:"3",
						value:"3"
					},{
						text:"其他",
						value:"其他"
					}],
				blood_types: [
					{
						value:"0",
						text:"A"
					},
					{
						value:"1",
						text:"B"
					},
					{
						value:"2",
						text:"AB"
					},
					{
						value:"3",
						text:"O"
					},
					{
						value:"4",
						text:"其他"
					}]
			}
		},
		methods:{
			clickNext (){
				window.localStorage.form = JSON.stringify(this.form);
			}
		}
	}
</script>
<style scope>
.age-input .el-input{
	width: 25%;
	margin-right: 5px;
}
.age-input .el-input input{
	height: 30px;
}
.el-btn .el-button{
	padding: 5px 10px;
}
.mint-cell-title{
	width: 80px !important;
	flex: none !important;
}
</style>



